b{box-shadow:1px 1px 1px #111,0 0 1px #1e1e1e;border:0 solid #fff;height:25px;width:8px;border-radius:0;background:blue;cursor:pointer;-webkit-appearance:none;margin-top:-11px}[type='range']:focus::-webkit-slider-runnable-track{background:#4f4f4f}[type='range']::-moz-range-track{width:100%;height:5px;cursor:pointer;-moz-transition:all 0.2s ease;transition:all 0.2s ease;box-shadow:2px 2px 2px #222,0 0 2px #2f2f2f;background:#424242;border:1px solid #000;border-radius:0}[type='range']::-moz-range-thumb{box-shadow:1px 1px 1px #111,0 0 1px #1e1e1e;border:0 solid #fff;height:25px;width:8px;border-radius:0;background:blue;cursor:pointer}[type='range']::-ms-track{width:100%;height:5px;cursor:pointer;-ms-transition:all 0.2s ease;transition:all 0.2s ease;background:transparent;border-color:transparent;border-width:8px 0;color:transparent}[type='range']::-ms-fill-lower{box-shadow:2px 2px 2px #222,0 0 2px #2f2f2f;background:#353535;border:1px solid #000;border-radius:0}[type='range']::-ms-fill-upper{box-shadow:2px 2px 2px #222,0 0 2px #2f2f2f;background:#424242;border:1px solid #000;border-radius:0}[type='range']::-ms-thumb{box-shadow:1px 1px 1px #111,0 0 1px #1e1e1e;border:0 solid #fff;height:25px;width:8px;border-radius:0;background:blue;cursor:pointer}[type='range']:focus::-ms-fill-lower{background:#424242}[type='range']:focus::-ms-fill-upper{background:#4f4f4f}

